import React from 'react';
import GoogleMapReact from 'google-map-react';
import styled from 'styled-components';
import constants from '../../constants/shops';

const GoogleMapContainer = styled.div`
  height: ${props => props.height + 'px'};
  width: 100%;
`;

const API_KEY = 'AIzaSyDiulZlopt_Zx5pEGGI7qRxJh3pLmznro8';

const GoogleMapComponent = ({ height, isRenderPolygon, mapState }) => {
  return (
    <>
      <GoogleMapContainer height={height}>
        <GoogleMapReact
          bootstrapURLKeys={{ key: API_KEY }}
          defaultCenter={mapState.center}
          defaultZoom={mapState.zoom}
        >

          {isRenderPolygon && constants.coverageMarkers.map(marker => (
            <span
              className="icon map-pin-dark big"
              lat={marker.lat}
              lng={marker.lng}
              key={marker.lat + marker.lan}
            />
          ))}

          {!isRenderPolygon && constants.shopsMarkers.map(marker => (
            <span
              className="icon map-pin-dark big"
              lat={marker.lat}
              lng={marker.lng}
              key={marker.lat + marker.lan}
            />
          ))}

          {!isRenderPolygon && constants.easyPayOfficeMarkers.map(office => (
            <span
              className="icon easypay big"
              lat={office.marker.lat}
              lng={office.marker.lng}
              key={office.marker.lat + office.marker.lan}
            />
          ))}

        </GoogleMapReact>
      </GoogleMapContainer>
    </>
  );
}

export default GoogleMapComponent;