import React from 'react';
import styled from 'styled-components';
import Layout from '../components/layout';
import SEO from '../components/seo';
import SectionTitles from '../components/shared/section-titles';
import constants from '../constants/shops';
import GoogleMapComponent from '../components/shared/google-map';
import meta from "../constants/meta-descriptions";

export const TitleH1 = styled.h1`
  @media screen and (max-width: 720px) {
    font-size: 30px;
  }
`;

const ShopPage = () => {
  const scrollToRef = (id) => window.scrollTo({ top: refs[id].current.offsetTop - 100, behavior: 'smooth' });
  const refs = constants.shops.map((_) => React.createRef());

  return (
    <>
      <Layout
        activeTab={'/magazini/'}
      >
        <div style={{ paddingTop: '12rem' }}>
          <SEO
            title={meta.shops.title}
            description={meta.shops.metaDescription}
          />
          <SectionTitles>
            <TitleH1>Нашите магазини в цяла<br />България</TitleH1>
            <hr className='center' />
          </SectionTitles>

          <div className='container text-center mt-5'>
            <h3>Бърз достъп</h3>

            <div className='row d-flex justify-content-center mt-4'>
              {constants.shops.map(({ city }, index) => (
                <div className='col-4 col-md-3 col-lg-2 my-2 my-md-0' key={index}>
                  <button className='clear'
                    onClick={() => {
                      scrollToRef(index);
                    }}
                  >
                    <p className='link'>{city}</p>
                  </button>
                </div>
              ))}

              <hr className='big-blue' />
              {constants.shops.map(({ places }, i) => {
                return (
                  <div className='w-100' key={i} ref={refs[i]}>
                    {places.map((place, index) => (
                      <div className='w-100' key={index}>
                        {(i !== 0 || index !== 0) && <hr className='small-blue' />}
                        <Place {...place} />
                      </div>))}
                  </div>)
              })}

            </div>
          </div>
        </div>
      </Layout>
    </>
  );
}

const Place = ({ city, address, telephone, working_hours, mapState, officeFeatures, easyPayOffice }) => {
  return (
    <>
      <div className='row w-100 py-2'>
        <div className='col-12 col-md-6 text-left'>
          <h2>{city}</h2>
          <div className='my-4'>
            <h3><span className='icon map-pin-shop mr-2'></span>Адрес:</h3>
            <p className='margin-left-offices'>{address}</p>
          </div>
          <div className='my-4'>
            <h3><span className='icon blue-phone mr-2'></span>Телефон:</h3>
            <a href={'tel:' + telephone} className='margin-left-offices'>{telephone}</a>
          </div>
          <div className='my-4'>
            <h3><span className='icon clock mr-2'></span>Работно време:</h3>
            <div className='margin-left-offices'>
              {working_hours}
            </div>
          </div>
        </div>

        <div className='col-12 col-md-6'>
          <GoogleMapComponent height={350} mapState={mapState} />
          <div className='row mt-4'>
            <div className='col-6 m-0 p-0 d-flex justify-content-start'>
              <div>
                <h3><span className='icon map-pin-dark mr-2'></span>Магазин на NetSurf:</h3>
                <ul className='blue-checks text-left'>
                  {officeFeatures.map((feature, index) => (
                    <li className='blue-checks' key={index}>
                      <p className='m-0 p-0'>{feature}</p>
                    </li>
                  ))}
                </ul>
              </div>
            </div>
            <div className='col-6 m-0 p-0 d-flex justify-content-start'>
              <div>
                <h3><span className='icon easypay mr-2'></span>Каса на Easypay:</h3>
                <ul className='blue-checks text-left'>
                  {easyPayOffice.map((feature, index) => (
                    <li className='blue-checks' key={index}>
                      <p>{feature}</p>
                    </li>
                  ))}
                </ul>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default ShopPage;